import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Configuracao_ouvidoria } from '../../../modelo/Configuracao_ouvidoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'configuracao_ouvidoria-dados-details',   templateUrl: 'configuracao_ouvidoria_dadosDetails.component.html' }) export class Configuracao_ouvidoriaDadosDetailsComponent implements OnInit {    configuracao_ouvidoria: Configuracao_ouvidoria;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.configuracao_ouvidoria.Configuracao_ouvidoria";                 let id = +params['id'];         this.configuracao_ouvidoria = new Configuracao_ouvidoria();         this.configuracao_ouvidoria.cd_configuracao_ouvidoria = id;         o.objetoJson = this.configuracao_ouvidoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a configuracao_ouvidoria. '; }      this.configuracao_ouvidoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


